import { Injectable }              from '@angular/core';
import { Headers,Http, Response,RequestOptions }          from '@angular/http';
import { AppConstants } from '../app.constant';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
@Injectable()

export class LocationService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private options = new RequestOptions({ headers: this.headers });
  constructor(private http: Http) {}

  insertLocation(insertLocation:object): Promise<any> {
    return this.http
      .post(AppConstants.serverUrl + '/location', insertLocation)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  updateLocation(location_id,editedLocation:object): Promise<any> {
     return this.http
      .put(AppConstants.serverUrl + '/updateLocation/'+location_id, editedLocation)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  getAllLocatoin(data:object): Promise<any> {
        let obj={
            params:data
        }
    return this.http
      .get(AppConstants.serverUrl + '/getLocation',obj)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  deleteLocation(location_id): Promise<any> {
    return this.http
      .delete(AppConstants.serverUrl + '/deleteLocation/'+location_id)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  getLocationDetails(location_id): Promise<any> {
    return this.http
      .get(AppConstants.serverUrl + '/getLocationDetails/'+location_id)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }


  private extractData(res: Response) {
    let body = res.json();
    return body || {};
  }

  private handleError(error: any): Promise<any> {
    return Promise.reject(error.message || error);
  }
}




