import { Injectable }              from '@angular/core';
import { Headers,Http, Response,RequestOptions }          from '@angular/http';
import { AppConstants } from '../app.constant';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
@Injectable()

export class ManageCompoffService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private options = new RequestOptions({ headers: this.headers });
  constructor(private http: Http) {}


  getcompoff(tenant:object): Promise<any> {
    let obj={
      params:tenant
    }
    return this.http
      .get(AppConstants.serverUrl + '/getempcompoff',obj)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  acceptcompoff(acceptParam: object): Promise<any> {
     return this.http
      .put(AppConstants.serverUrl + '/accept_compoff', acceptParam)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  rejectcompoff(rejectParam: object): Promise<any> {
     return this.http
      .put(AppConstants.serverUrl + '/reject_compoff', rejectParam)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }



  private extractData(res: Response) {
    let body = res.json();
    return body || {};
  }

  private handleError(error: any): Promise<any> {
    return Promise.reject(error.message || error);
  }
}




