import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { routing } from './permissionrequest.routing';
import { SharedModule } from '../app.sharedmodule';

import { PermissionRequestComponent } from './permissionrequest.component';
import { AddPermissionRequestComponent,EditPermissionRequestComponent } from './permissionrequest.component';

import { PermissionRequestService } from './permissionrequest.service';
import { DataTableModule } from 'angular-4-data-table';
import { ModalModule } from "ng2-modal";
import { MyDatePickerModule } from 'mydatepicker';
//import { dateFormatPipe,skipUnderScore,removeSpaces,capitalise,numberFixedLen,normalDate,keyValues,toPercent,Pairs } from '../app.filter';

@NgModule({
  imports: [routing, BrowserModule,FormsModule,HttpModule,DataTableModule,ModalModule,MyDatePickerModule,SharedModule.forRoot()],
  declarations: [PermissionRequestComponent,AddPermissionRequestComponent,EditPermissionRequestComponent],
  providers:[PermissionRequestService]
})
export class PermissionRequestModule {}



