import { Component, OnInit } from '@angular/core';
import {AppComponent} from "../app.component";
import { CoolLocalStorage } from 'angular2-cool-storage';
import {EmployeeHistoryService} from "./employee_history.service";
import {Router, ActivatedRoute, Params} from '@angular/router';
import {AppConstants, ErrorConstants} from "../app.constant";
import {AppService} from "../app.service";
import {IMyDpOptions,IMyDate, IMyDateModel} from 'mydatepicker';
import { DataTableResource } from 'angular-4-data-table';
import * as moment from 'moment/moment';

@Component({
  selector: 'app-summary',
  templateUrl: './employee_history.component.html',
})
export class EmployeeHistoryComponent implements OnInit {

   errMessage = "";
   showrecords:boolean;
    success:boolean;
    success_message:string;
    error={};
    protected storedDetail;
    screen_name;
    isLoading:boolean;
    itemCount=0;
    items = [];
    error_status:boolean;
  employeeHistory;


  constructor(
        private appComponent:AppComponent,
        public localStorage: CoolLocalStorage,
         protected employeeHistoryService:EmployeeHistoryService,
        protected route :ActivatedRoute,
        protected router :Router,
        protected activatedRoute: ActivatedRoute,
        protected appservice:AppService
    ){
        this.appComponent.showIncludes=true;

    }

    ngOnInit() {
        this.errMessage = "";
        let success_msg = this.appservice.getter();
        this.storedDetail=this.localStorage.getObject('storedDetails');
       this.getemployeeHistory();
        if(success_msg){
            if(success_msg.success){
                this.success = true;
                this.success_message = success_msg.message;
                let success = {success:false,message:""};
                this.appservice.setter(success);
            }else{
                this.success = false;
            }

        }else{
            this.success = false;
        }
    }

  reloadItems(params) {
    this.employeeHistory.query(params).then(items => this.items = items);
  }


  getemployeeHistory(){
    let details = {"employee_id":this.storedDetail.employee_id};
    this.isLoading = true;
    this.employeeHistoryService.getempHistory(details).then( (res)=> {
      console.log(res);
      if(res.message == 'No records found') {
        console.log(res);
        this.isLoading = false;
        this.showrecords=true;
        this.success_message="No Records Found";
        this.itemCount = 0;
      }else {
        this.isLoading = false;
        this.employeeHistory = res.data;
        console.log(this.employeeHistory);
        this.employeeHistory = new DataTableResource(res.data);
        this.itemCount = res.data.length;
      }
    }).catch(function(err){
      console.log(err);
    })
  }

}





